<template>
  <page500
    :img-url="statePageConfig.imgUrl"
    :msg="statePageConfig.msg"
    :my-class="statePageConfig.myClass"
    :button-config="statePageConfig.buttonConfig"
  />
</template>

<script>
import page500 from 'src/components/exception/500-page/500-page.vue';
export default {
  components: {
    page500
  },
  data() {
    return {
      statePageConfig: {
        imgUrl: require('../../../public/images/500.gif'),
        msg: {
          title: '',
          content: '提示文字可自定义',
          notice: ''
        },
        myClass: '',
        buttonConfig: {
          name: '返回首页',
          callback: () => {
            this.$router.push({
              path: '/home'
            });
          }
        }
      }
    };
  }
};
</script>



