<template>
  <div class="page-500">
    <div class="left">
      <div class="text-align-left">
        <gs-button
          v-if="buttonConfig"
          @click="buttonConfig.callback"
        >
          {{ buttonConfig.name||'返回' }}
        </gs-button>
        <h1 class="note-error">{{ msg.title||'500' }}</h1>
        <h2 class="note-result">{{ msg.content||'服务器故障' }}</h2>
        <h4>{{ msg.notice||'如有问题请联系管理员' }}</h4>
      </div>
    </div>
    <div class="right" />
  </div>
</template>

<script>
import './500-page.scss';

export default {
  props: {
    'imgUrl': {
      type: String,
      required: true,
      default: ''
    },
    'msg': {
      type: Object,
      default: () => {
        return {
          title: null,
          content: null,
          notice: null
        };
      }
    },
    'myClass': {
      type: String,
      default: ''
    },
    'buttonConfig': {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {};
  }
};
</script>



